import React from 'react';
import {
  SafeAreaView,
  ScrollView,
  StatusBar,
  StyleSheet,
  Text,
  TextInput,
  TouchableOpacity,
  useColorScheme,
  View,
} from 'react-native';

import {Colors, Header} from 'react-native/Libraries/NewAppScreen';
import Section from '../components/Section/Section';

function AuthScreen(): React.JSX.Element {
  const isDarkMode = useColorScheme() === 'dark';

  const backgroundStyle = {
    backgroundColor: isDarkMode ? Colors.darker : Colors.lighter,
  };

  const onLogin = () => console.log('onLogin');
  const onSignUp = () => console.log('onSignUp');

  return (
    <SafeAreaView style={backgroundStyle}>
      <StatusBar
        barStyle={isDarkMode ? 'light-content' : 'dark-content'}
        backgroundColor={backgroundStyle.backgroundColor}
      />
      <ScrollView
        contentInsetAdjustmentBehavior="automatic"
        style={backgroundStyle}>
        <Header />
        <View
          style={{
            backgroundColor: isDarkMode ? Colors.black : Colors.white,
          }}>
          <Section title="Login/Register">
            <View style={{}}>
              <TextInput placeholder="account" style={styles.textInput} />
              <TextInput
                secureTextEntry
                placeholder="password"
                style={styles.textInput}
              />
            </View>
          </Section>
          <View
            style={{
              marginVertical: 24,
              justifyContent: 'center',
              alignItems: 'center',
            }}>
            <TouchableOpacity
              style={{backgroundColor: 'blue', padding: 8}}
              onPress={onLogin}>
              <Text style={{color: 'white'}}>Login</Text>
            </TouchableOpacity>
            <Text>or</Text>
            <TouchableOpacity
              style={{backgroundColor: 'green', padding: 8}}
              onPress={onSignUp}>
              <Text style={{color: 'white'}}>SignUp</Text>
            </TouchableOpacity>
          </View>
        </View>
      </ScrollView>
    </SafeAreaView>
  );
}

const styles = StyleSheet.create({
  textInput: {
    borderBottomWidth: 1,
  },
  highlight: {
    fontWeight: '700',
  },
});

export default AuthScreen;
